    infostructureUp() {
      this.up = true;
      let temp = [...this.infostructures];

      this.infostructures.splice(temp.length - 1, 1);
      this.infostructures.unshift(temp[temp.length - 1]);
    },
    infostructureDown() {
      this.up = false;
      let temp = [...this.infostructures];

      this.infostructures.splice(0, 1);
      this.infostructures.push(temp[0]);
