      document.getElementById(this.planImgsEl).scrollLeft = scrollVal;
    },
    plansLeft() {
      const scrollVal =
        document.getElementById(this.planImgsEl).scrollLeft -
        this.planImgsScrollStep;
      document.getElementById(this.planImgsEl).scrollLeft = scrollVal;
    },
    changeActivePlan(index) {
      this.plans.forEach((f) => {
        f.active = false;
      });
      this.plans[index].active = true;
    },
  },
  computed: {
    currentInfostructure() {
      return this.infostructures[this.infostructureActiveIndex]
        ? this.infostructures[this.infostructureActiveIndex].id
        : 1;
    },
    currentPlan() {
      return this.plans.find((p) => p.active);
    },
    flats() {
      let array = [];
      for (let i = 1; i <= this.currentPlan.flatCount; i++) {
        array.push(i);
      }
      return array;
    },
  },
